::-webkit-scrollbar {
  width: 0.5rem;
  display: none;
}
@media (min-width: 1024px) {
  ::-webkit-scrollbar {
    display: block;
  }
}

::-webkit-scrollbar-thumb {
  background: hsl(217deg, 33%, 17%);
  border-radius: 0.5rem;
}
::-webkit-scrollbar-thumb:hover {
  background: hsla(225deg, 20%, 61%, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  list-style: none;
  color: hsl(210deg, 40%, 98%);
  background-color: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Heebo", sans-serif;
  background-color: hsl(222deg, 46%, 11%);
  width: 100%;
}
body.scroll__lock {
  overflow: hidden;
}

img {
  display: block;
}

input {
  padding: 0.5rem;
  border: 1px solid;
  border-color: hsl(214deg, 20%, 34%);
  color: hsl(215deg, 20%, 65%);
  border-radius: 0.5rem;
  transition: all 0.2s ease;
}
input:focus {
  color: hsl(210deg, 40%, 98%);
  background-color: hsl(217deg, 33%, 17%);
  border-color: hsl(215deg, 20%, 65%);
}
input:focus::placeholder {
  visibility: hidden;
}

.main__content {
  display: flex;
  flex-direction: column;
  margin-inline: 10%;
  gap: 5rem 0;
}

.wrapper {
  position: relative;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: hsl(222deg, 46%, 11%);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader.fadeout {
  z-index: -1;
  opacity: 0;
  transition: all 0.2s ease;
}
.loader img {
  width: 4rem;
  height: auto;
  animation: loader 1.5s infinite ease;
}
@keyframes loader {
  to {
    transform: rotate(1turn);
  }
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header__main {
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 0.5rem 1rem hsla(0deg, 0%, 0%, 0.1);
  padding: 0.7rem 5%;
  z-index: 1000;
  transition: all 0.25s ease;
}
@media (min-width: 1024px) {
